import React, { useState } from "react";
import { addVolunteer } from "../../../../../components/services/volunteerservices";

const AddVolunteerPage = () => {
  const [volunteer, setVolunteer] = useState({
    name: "",
    email: "",
    phone: "",
    address: "",
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setVolunteer({ ...volunteer, [e.target.name]: e.target.value });
  };

  const handleSubmit = async () => {
    if (!volunteer.name || !volunteer.email || !volunteer.phone || !volunteer.address) {
      alert("Please fill all fields!");
      return;
    }

    const response = await addVolunteer(volunteer);
    if (response.success) {
      alert("Volunteer added successfully!");
      setVolunteer({ name: "", email: "", phone: "", address: "" }); // Reset form
    } else {
      alert("Error adding volunteer!");
    }
  };

  return (
    <div>
      <h2>Add Volunteer</h2>
      <input type="text" name="name" placeholder="Name" value={volunteer.name} onChange={handleChange} />
      <input type="email" name="email" placeholder="Email" value={volunteer.email} onChange={handleChange} />
      <input type="text" name="phone" placeholder="Phone" value={volunteer.phone} onChange={handleChange} />
      <input type="text" name="address" placeholder="Address" value={volunteer.address} onChange={handleChange} />
      <button onClick={handleSubmit}>Add Volunteer</button>
    </div>
  );
};

export default AddVolunteerPage;
